___CSS_LOADER_EXPORT___.push([module.id, ".tagify__input:before{line-height:normal}", "",{"version":3,"sources":["webpack://./resources/js/Shared/Tags.svelte"],"names":[],"mappings":"AAiGY,sBAAyB,kBACT","sourcesContent":["<script>\r\n    import Tagify from '@yaireo/tagify'\r\n\r\n    import '@yaireo/tagify/dist/tagify.css'\r\n    import InputError from '@/Shared/InputError'\r\n\r\n    export let error\r\n    export let placeholder\r\n    export let whitelist\r\n    export let tags\r\n\r\n    let inputElm, tagify\r\n\r\n    let selectedValues = null\r\n\r\n    if (tags) {\r\n        selectedValues = JSON.parse(tags)\r\n            .map(function (item) {\r\n                return item.value\r\n            })\r\n            .join(',')\r\n    }\r\n\r\n    function init() {\r\n        inputElm = document.querySelector('input[name=tags]')\r\n\r\n        // initialize Tagify on the above input node reference\r\n        tagify = new Tagify(inputElm, {\r\n            enforceWhitelist: true,\r\n\r\n            // make an array from the initial input value\r\n            whitelist: inputElm.value.trim().split(/\\s*,\\s*/),\r\n        })\r\n\r\n        // Chainable event listeners\r\n        tagify\r\n            .on('add', onAddTag)\r\n            .on('remove', onRemoveTag)\r\n            .on('input', onInput)\r\n            .on('edit', onTagEdit)\r\n            .on('dropdown:hide dropdown:show', (e) => console.log(e.type))\r\n    }\r\n\r\n    var mockAjax = (function mockAjax() {\r\n        var timeout\r\n        return function (duration) {\r\n            clearTimeout(timeout) // abort last request\r\n            return new Promise(function (resolve, reject) {\r\n                timeout = setTimeout(resolve, duration || 700, whitelist)\r\n            })\r\n        }\r\n    })()\r\n\r\n    // tag added callback\r\n    function onAddTag(e) {\r\n        tags = inputElm.value\r\n    }\r\n\r\n    // tag remvoed callback\r\n    function onRemoveTag(e) {\r\n        tags = inputElm.value\r\n    }\r\n\r\n    // on character(s) added/removed (user is typing/deleting)\r\n    function onInput(e) {\r\n        // console.log('onInput: ', e.detail)\r\n        tagify.settings.whitelist.length = 0 // reset current whitelist\r\n        tagify.loading(true).dropdown.hide.call(tagify) // show the loader animation\r\n\r\n        // get new whitelist from a delayed mocked request (Promise)\r\n        mockAjax().then(function (result) {\r\n            // replace tagify \"whitelist\" array values with new values\r\n            // and add back the ones already choses as Tags\r\n            if (result != undefined) {\r\n                tagify.settings.whitelist.push(...result, ...tagify.value)\r\n            }\r\n\r\n            // render the suggestions dropdown.\r\n            tagify.loading(false).dropdown.show.call(tagify, e.detail.value)\r\n        })\r\n    }\r\n\r\n    function onTagEdit(e) {\r\n        tags = inputElm.value\r\n    }\r\n\r\n    $: props = {\r\n        ...$$restProps,\r\n        class: `tagify-input bg-white w-full border-gray-300 rounded-md shadow-sm focus:ring focus:ring-opacity-50 focus:border-indigo-200 focus:ring-indigo-200 ${$$restProps.class || ''}`,\r\n    }\r\n</script>\r\n\r\n<input on:load={init()} name=\"tags\" {...props} {placeholder} value={selectedValues} />\r\n\r\n<InputError message={error} />\r\n\r\n<style>\r\n    :global(.tagify__input::before) {\r\n        line-height: initial;\r\n    }\r\n</style>\r\n"],"sourceRoot":""}]);
